.thank-you-page-wrapper {
    background: rgb(251, 251, 251);
    div {
        background: rgb(251, 251, 251);
    }
    .go-to-action {
        &:hover {
            color: #fcae2a !important;
            opacity: 0.7;
        }
    }
}

body {
    background: rgb(251, 251, 251) !important;
    .order-total-span {
        font-family: 'Roboto-medium';
        font-size: 14px;
        color: var(--black) !important;
    }
    @media only screen and (max-width: 680px) {
        .order-total-span {
            font-family: 'Roboto-medium';
            font-size: 12px;
            color: var(--black) !important;
        }
      }
}
